<template>
  <div>
    <multiselect
      v-model="value"
      :options="options"
      :multiple="true"
    ></multiselect>
  </div>
</template>

<script>
import Multiselect from "vue-multiselect";
import allCountries from "../utils/allCountries";

export default {
  components: { Multiselect },
  data() {
    return {
      value: null,
      options: []
    };
  },
  methods: {},
  created() {
    this.options = allCountries;
  }
};
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>

